<style scoped>
	.container{
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		margin: auto;
		background-color: #fff;
	}
	
	.signIn,.signUp{
		width: 4.2rem;
		height: 0.8rem;
		line-height: 0.8rem;
		text-align: center;
		border-radius: 4px;
		margin:0.4rem auto 0;
		background-color: #000;
		color: #fff;
		font-size: 0.4rem;
	}
	.copyRight{
		position: absolute;
		bottom: 0.4rem;
		left: 0;
		right: 0;
		margin: auto;
		font-size: 0.3rem;
	    text-align: center;
	}
	.signUpPart,.signInPart{
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		margin: auto;
		height: 7rem;
	}

	.signList{
		width: 6rem;
		margin: 0 auto 0.3rem;
		height: 0.8rem;
	}
	.signList>span{
		width: 1.5rem;
		font-size: 0.3rem;
		float: left;
		text-align: right;
		line-height: 0.7rem;
		margin-right: 0.3rem;
	}
	.signList>input{
		float: left;
		width: 4rem;
		height: 0.7rem;
		border: 1px solid #b6b6b6;
		font-size: 0.3rem;
	}
	.fadeOut-enter-active, .fadeOut-leave-active {
	  transition: opacity .7s;
	}
	.fadeOut-enter, .fadeOut-leave-to /* .fade-leave-active below version 2.1.8 */ {
	  opacity: 0;
	}
</style>
<template>
	
	<div class="container">
		<myCanvas :dotsNum='dotsNum' :isColor='false'></myCanvas>
		<transition name='fadeOut'>
			<dispearPart v-show='dispear'></dispearPart>
		</transition>
		<!-- signUpPart为登录窗口 -->
		
		<div class="signUpPart animated" :class='{bounceOutLeft:islideInUp}' v-show='signUpShow'>
			<div class="signList">
				<span>手机号</span>
				<input type="text" placeholder="请输入您的手机号" class="ipt">
			</div>
			<div class="signList">
				<span>密码</span>
				<input type="password" class="ipt">
			</div>
			<span class="signUp animated bounceOutLeft">登录</span>
		</div>

		<!-- signInPart为注册窗口-->
		<div class="signInPart" v-show='signInShow'>
			<div class="signList">
				<span>用户名</span>
				<input type="text" class="ipt" placeholder="请输入您的手机号">
			</div>
			<div class="signList">
				<span>手机号</span>
				<input type="text" class="ipt" placeholder="请输入您的手机号">
			</div>
			<div class="signList">
				<span>密码</span>
				<input type="password"  class="ipt">
			</div>
			<div class="signList">
				<span>确认密码</span>
				<input type="password" class="ipt">
			</div>
			<span class="signIn">注册</span>
		</div>


		
		
		
		<span class="copyRight">@copyright via Lokep</span>
	</div>

</template>
<script>
	import myCanvas from './canvas.vue'
	import dispearPart from './dispearPart.vue'
	export default{
		name:'Sign',
		data(){
			return{
				dispear:true,
				signInShow:false,
				signUpShow:false,
				dotsNum: 30,
      			roundColor: "red",
      			lineColor: "rgba(68,68,68,.4)",
      			islideInUp:false
			}
		},
		components:{
			myCanvas,
			dispearPart
		},
		mounted(){
			let _this = this;
			const timeOut = setTimeout(function(){
                _this.dispear=false;
                _this.signUpShow=true;
                _this.islideInUp=true;
			}, 2500);
		},
		methods:{
			
		}

	}
</script>



