<style>
	.message{
		height: 3.6rem;
		background-color: #fff;
	}
	.messageTitle{
		height: 1.25rem;
	}
	.messageUserName{
		float: left;
		line-height: 0.9rem;
		margin-top: 0.25rem;
		font-size: 0.32rem;
	}
	.messageTxt{
		font-size: 0.3rem;
		line-height: 2;
		color: #000;
		margin-left: 0.5rem;
		/*margin-top: -0.3rem;*/
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	.messageSubmitTime{
		float: right;
		font-size: 0.28rem;
		color:rgb(136,136,136);
		line-height: 0.9rem;
		margin-right: 0.3rem;
		margin-top: 0.3rem;
	}
	.blogShort{
		width: 7rem;
		height: 1.4rem;
		background-color: #f5f5f5;
		margin:0 0 0.3rem 0.5rem;
	}
	.blogInfoImg{
		width: 1.4rem;
		height: 1.4rem;
		overflow: hidden;
		float: left;
	}
	.blogInfoImg>img{
		/*max-width: 1.4rem;*/
		max-height:1.4rem;
		margin:0 auto;
	}
	.blogInfo{
		float: left;
		width: 5rem;
		margin-left: 0.3rem
	}
	.blogInfoName{
		color: #0079ff;
		font-size: 0.32rem;
    	margin-top: 0.05rem;
	}
	.blogInfoTxt{
		font-size: 0.27rem;
		color: #666;
		margin-top: 0.05rem;
	}
</style>
