	<div class="message">
		<div class="messageTitle">
			<div class="userImg" style="margin-top: 0.25rem">
				<img src="@/assets/img/userimg.jpg" alt="">
			</div>
			<h4 class="messageUserName">Lokep</h4>
			<span class="messageSubmitTime">8分钟前</span>
		</div>
		<div class="messageContent">
			<span class="messageTxt">因为我是社会主义接班人</span>
			<div class="blogShort">
				<div class="blogInfoImg">
					<img src="@/assets/img/userimg.jpg" alt="微博内容简介">
				</div>
				<div class="blogInfo">
					<span class="blogInfoName">@Lokep</span>
					<p class="blogInfoTxt">我用Vue写了一个微博客户端，当然现在还在开发阶段</p>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	export default{
		name:'message',
		data(){
			return{

			}
		},
		components:{

		},
		mounted(){

		},
		methods:{

		}
	}
</script>


