<style>
.dispearPart{
	position: fixed;
	margin:1.9rem auto 1.2rem;
	width: 100%;
	height: 100%;
	background-color: rgba(255,255,255,.4);
	transition: opacity .7s;
}
.dispearPart>h4,.dispearPart>span{
	text-align: center;
}
.dispearPart>h4{
	font-size: 0.66rem;
	margin:0 auto 0.5rem;
	background-image: url(../assets/img/logo.png);
	height: 0.8rem;
	width: 4.5rem;
	background-position: 5px center;
	background-size: 255%;
}
.dispearPart>span{
	font-size: 0.3rem;
	margin-bottom: 0.36rem;
}	
</style>
<template>
	<!--dispearPart为简述话语，当点击登录或注册后隐藏-->
	<div class="dispearPart">
		<h4></h4>
		<span>有时候啊 你总是在追赶前面的人</span>
		<span> 总是抱怨自己为什么不能再努力一点</span>
		<span>累了你可以停下来 看看原来的自己</span>
		<span>其实 你已经很了不起了</span>
	</div>
</template>
<script>
	export default{
		name:'dispearPart',
		data(){
			return{

			}
		},
		mounted(){

		},
		methods:{

		}
	}
</script>


