		border-radius: 0.04rem;
		margin-left: 0.3rem;
	}
	.searchMenu{
		height: 0.7rem;
	}
	.searchMenu>span{
		line-height: 0.7rem;
		width: 1rem;
		padding-left: 0.5rem;
		font-size: 0.3rem;
		float: left;
