				<span class="searchComment" :class="{searchcommentPress:iscommentPress}" @click='commentPressed'>(1998)</span>
			</div>
		</div>
	</div>
</template>
<script>
	export default{
		name:'searchContent',
		data(){
			return{
				isLikePress:false,
				iscommentPress:false,
			}
		},
		mounted(){

		},
		methods:{
			LikePressed:function(){
				this.isLikePress=!this.isLikePress;
			},
			commentPressed:function(){
				this.iscommentPress=!this.iscommentPress;
			}
		}
	}
</script>


