<style>
	.Index{
		width: 100%;
		height: auto;
	}
	.indexHeader{
		height: 1.1rem;
		background-color:#fff; 
	}
	.userImg{
		border-radius: 90px;
		width: 0.9rem;
		height: 0.9rem; 
		margin: 0.1rem 0.3rem;
		float: left;
		position: relative;
		overflow: hidden;
	}
	.userImg>img{
		position: absolute;
		height: 0.9rem;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		margin: auto;
	}
	.userName{
		font-size: 0.36rem;
		line-height: 1.1rem;
		font-weight: 600;
		float: left;
	}
	.userFresh{
		height: 0.45rem;
		width: 0.45rem;
		margin: 0.325rem 0.3rem;
		float: right;
	}
	.tabBar{
		width: 100%;
		height: 1rem;
		background-color: #f4f5f5;
		margin-bottom: 0.1rem;
		border-bottom: 0.01rem solid #f5f6f6;
	}
	.tabBar>li{
		float: left;
		width: 33.3%;
		width: calc(100% / 3);
		height: 1rem;
		line-height: 1rem;
		text-align: center;
		font-size: 0.3rem;
	}
	.tabBar>.tabActive{
		border-bottom: 2px solid #0079ff;
		color: #0079ff;
	}
	
</style>
<template>
	<div class="Index">
		<div class="indexHeader">
			<div class="userImg">
				<img src="@/assets/img/userimg.jpg" alt="">
			</div>
			<span class="userName">Lokep</span>
			<img class="userFresh" src="../assets/img/refresh.png" alt="">
		</div>
		<ul class="tabBar">
			<!-- <li :class="{tabActive:true}" @click='shiftControl'>主页</li>
			<li :class="{tabActive:false}" @click='shiftControl'>探索</li>
			<li :class="{tabActive:false}" @click='shiftControl'>消息</li> -->

			<li :class='{tabActive:index == isTabClick}' v-for='(item,index) in tabBarTxt' @click='shiftControl(index)'>{{item}}</li>

		</ul>
		<blogContent v-show='blogContentShow'></blogContent>
		<searchContent v-show='searchContentShow'></searchContent>
		<message v-show='messageShow'></message>
	</div>
</template>
<script>
	import blogContent from './blogContent.vue'
	import searchContent from './searchContent.vue'
	import message from'./message.vue'
	export default{
		name:'Index',
		data(){
			return{
				tabBarTxt:['主页','探索','消息'],
				isTabClick:0,
				blogContentShow:false,
				searchContentShow:false,
				messageShow:false
			}
		},
		components:{
			blogContent,
			searchContent,
			message,
		},
		mounted(){
			// this.blogContentShow=true
		},
		methods:{
			shiftControl:function(index){
				this.isTabClick=index;
				switch(index){
					case 0:
						this.blogContent=true;
						this.searchContentShow=false;
						this.messageShow=false;
						console.log(this.blogContent);
						break;
					case 1:
						this.blogContent=false;
						this.searchContentShow=true;
						this.messageShow=false;
						break;
					case 2:
						this.blogContent=false;
						this.searchContentShow=false;
						this.messageShow=true;
						break;
					default:
						this.blogContent=true;
						this.searchContentShow=false;
						this.messageShow=false;
						break;
				}
				console.log(this.blogContent,this.searchContentShow,this.messageShow);
			}
		}
	}
</script>


